<template>
    <div id="music" class="music">
      <!-- 播放器 -->
      <audio :src="currentSong" autoplay controls="controls" @ended="nextong()"> </audio>
      <ul>
        <li v-for="(item,index) in musicList" @click="clickHandle(index)">
          <span v-if="currentIndex==index" style="color:goldenrod">歌名:{{item.name}}---{{item.author}}</span>
          <span v-else>歌名:{{item.name}}---{{item.author}}</span>
        </li>
      </ul>
    </div>
</template>

<script>
    import api from '../../api/importantjs/apis.js'
    // import '../../audioplayer/js/jquery.min.js'
    // import  '../../audioplayer/fonts/iconfont.css'
    // import '../../audioplayer//css/reset.css'
    // import '../../audioplayer/css/player.css'
    // import '../../audioplayer/js/utill.js'
    // import '../../audioplayer/js/player.js'
    
    export default {
        name: 'audioList',
        data() {
            return {
                musicList:[],
                status:true,
                currentIndex:0,
                currentSong:''
            }
        },
       
        methods: {
            //获取用户列表
            getMusic() {
                let t = this;
                t.listLoading = true;
                api.getMusicList().then((res) => {
                    if (res.data.code==200) {
                        let result = res.data.data;
                        t.musicList=result.data;
                        length=result.data.length;
                        t.total=parseInt(Math.random()*(length+1),10);
                        t.path=result.data[t.total].path;
                        t.currentIndex=t.total;
                        this.currentSong = this.musicList[t.currentIndex].path;
                        t.listLoading = false;
                    }
                });
            },
            clickHandle:function (index) {
                this.currentIndex=index
                this.currentSong = this.musicList[index].path;
                console.log(this.musicList[index].path)
            },
            nextSong:function () {
                if(this.currentIndex==this.musicList.length-1){
                    this.currentIndex=-1;
                }
                this.currentIndex++;
                console.log(this.currentIndex);
            }
        },
        mounted() {
            this.getMusic();
        },
    }


</script>

<style scoped>
</style>





