<template>
  <div>
    <head-list></head-list>
    <div class="detail-select">
      <div class="detail-container max-w">
        <div class="detail-title">
          <p>{{context.title}} </p>
          <p>{{context.created_at}}</p>  
        </div>
        <mavon-editor
            id="md" class="md"
          :value= "context.content"
          :subfield = "prop.subfield"
          :defaultOpen = "prop.defaultOpen"
          :toolbarsFlag = "prop.toolbarsFlag"
          :editable= "prop.editable"
          :scrollStyle= "prop.scrollStyle"
        ></mavon-editor>
      </div>
    </div>
    <div class="comment">
      <div class="comment" v-if="commentList.length">
      
      </div>
      <div class="comment" v-else>
      
      </div>
    </div>
    <foot-list></foot-list>
  </div>
</template>

<script>
import api from '../../api/importantjs/apis.js'
import headList from './header.vue'
import footList from './footer.vue'
export default {
  name: 'detail',
  components: {
    headList,
    footList
  },
  props: {
    msg: String
  },
  data(){
    return {
      context: '',
      articleDetail: '',
      prop: {
        subfield: false,// 单双栏模式
        defaultOpen: 'preview',//edit： 默认展示编辑区域 ， preview： 默认展示预览区域 
        editable: false,
        toolbarsFlag: false,
        scrollStyle: true
      },
      commentList:[]
    }
  },
  methods: {
    getDetails(){
      let t = this;
      let id = t.$route.query.id;
      api.getDetail({
        id: id
      }).then((res)=>{
        console.log(res)
        if(res && res.data && res.data && res.data.data){
          let result = res.data.data;
          t.context = result
        }
      })
    }
  },
  mounted() {
    let t = this;
    t.getDetails();
  },
}
</script>
<style scoped>

</style>


