<template>
  <div> 
    <div class="header">
      <div class="flex-between">
        <span @click="(show = !show)"><i class="iconfont icon-daohang"></i></span>
        <span>cfun个人博客</span>
        <div>
          
        </div> 
        <span @click="(showAudio = !showAudio)"><i class="iconfont icon-yinle"></i></span>
      </div>
      <div class="header-nav">
        <el-collapse-transition>
          <div v-show="show">
            <div class="transition-box">
              <p><router-link :to="{name:''}" target='_blank' >主页</router-link></p>
              <p><router-link :to="{name:''}" target='_blank' >数据库</router-link></p>
              <p><router-link :to="{name:''}" target='_blank' >前端</router-link></p>
              <p><router-link :to="{name:''}" target='_blank' >后台</router-link></p>
              <p><router-link :to="{name:''}" target='_blank' >加入我们</router-link></p>
            </div>
          </div>
        </el-collapse-transition>
      </div>
      <div :class="['header-audio',{'header-audio-hidden': showAudio}]" >
        <audio-list></audio-list>
      </div>
    </div>
  </div> 
</template>

<script>
import api from './../../api/importantjs/apis.js'
import audioList from './audio.vue'
// @node-click="handleNodeClick"

// <div class="nav" v-if="show">
//       <div class="header-container">
//         <div class="close" @click="(show = !show)"><i class="iconfont icon-chenghao"></i></div>
//         <el-tree :data="list" :props="defaultProps" ></el-tree>
//         <div class="title-l">关于我</div>
//       </div> 
//     </div> 
export default {
  name: 'headList',
    components: {
        audioList,
    },
  data(){
    return {
      show: false,
      showAudio:false,
      list: [],
      defaultProps: {
          children: 'children',
          label: 'label'
        }
    }
  },
  methods: {
    getRelate(){
      let t = this;
      api.getRelate({}).then((res)=>{
        console.log(res)
        if(res && res.data && res.data.data.length){
          let result = res.data.data;
          let list = []
          list = result.map((item)=>{
              return {label:item.category_name}
          })
          t.list = list
        }
      })
    },
  },
  mounted(){
    let t = this;
    t.getRelate()
  }
}
</script>
<style lang="css" slot-scoped>
  .header-container{
    max-width: 1000px;
    margin: 0 auto;
  }
</style>



