import axios from 'axios';

let base = 'https://api.cfun.vip/';

export const requestLogin = params => { return axios.post(`${base}/admin/login`, params).then(res => res.data); };

export const addArticle = params => { return axios.post(`${base}/admin/add`, params).then(res => res.data); };

export const getUserList = params => { return axios.get(`${base}/user/list`, { params: params }); };

export const deleteArticle = params => { return axios.get(`${base}/admin/delete`, { params: params }); };

export const detailArticle = params => { return axios.get(`${base}admin/detail`, { params: params }).then(res => res.data); };

export const addMusicMsg = params => { return axios.post(`${base}admin/addmusic`, { params: params }).then(res => res.data); };//添加音乐信息



